body {
	margin: 0;
	padding: 0;	
	box-sizing: border-box;
}
.App {
	margin: 30px auto;
	width: 30%;
	padding: 0;
	background-color: rgb(100, 134, 245);
	border-radius: 10px;
	font-family: 'Kanit', sans-serif;
	overflow: hidden;
}

.new-task-section {
	display: flex;
	margin: 20px auto;
	height: 80px;
	justify-content: space-between;
	width: 95%;
	align-items: center;
}

.new-task-section input {
	margin-left: 10px;
	width: 70%;
	border-radius: 5px;
	height: 2rem;
	border-style: none;
	background-color: lightblue;
	font-family: 'Kanit', sans-serif;
	font-size: 18px;
}

.new-task-section input:focus {
	outline: none;
}

.new-task-section button {
	margin: 0 12px;
	width: 20%;
	height: 37px;
	background-color: #079707;
	border: #079707;
	border-radius: 5px;
	font-size: 0.75rem;
	font-weight: 600;
	text-transform: uppercase;
	color: white;
	cursor: pointer;
	font-family: 'Kanit', sans-serif;
}

.new-task-section button:focus {
	outline: #389e0d;
	border-color: #389e0d;
}

.new-task-section button:hover {
	background-color: #44b315;
}

.new-task-section button:active {
	background-color:#44b315;
}

.task-list-section {
	margin: 20px auto;
	width: 91%;
	border-radius: 14px;
	background-color: #ffd666;
	overflow: hidden;
}

.task-list-title {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 40px;
	background-color: #fabc2c;
	font-size: larger;
	font-weight: bold;
}

.task-list-content{
	font-size: medium;
	margin: 12px 12px;
}

.task-list-item {
	display: flex;
	margin: 17px 0px; 
	padding-right: 5px;
	border-radius: 5px;
	justify-content: space-between;
	align-items: center;
	word-break: break-all;
}

.task-list-item .action-btn {
	display: flex;
	justify-content: space-around;
}

.task-list-item .action-btn .delete-icon{
	padding: 0px 5px;
}

.task-list-item .action-btn .delete-icon:focus {
	outline: none;
}

.task-list-item .action-btn .edit-icon{
	padding: 0px 5px;
}

.task-list-item .action-btn .edit-icon:focus {
	outline: none;
}

.edit-form {
	display: flex;
	justify-content: space-around;
}

.edit-form input {
	width: 100%;
	margin-right: 2px;
	border-radius: 5px;
	font-family: 'Kanit', sans-serif;
	font-size: 1rem;
	border-style: none;
}

.edit-form input:focus {
	outline: none;
}

.edit-form .btn-cancel {
	background-color: #f5222d;
	border-radius: 10px;
	border-style: none;
	font-family: 'Kanit', sans-serif;
	color: white;
	font-size: 0.9rem;
	margin: 0px 2px;
	cursor: pointer;
}

.edit-form .btn-cancel:focus {
	outline: none;
}

.edit-form .btn-cancel:hover {
	background-color: #e2454d;
}

.edit-form .btn-save {
	background-color: #079707;
	border-radius: 10px;
	border-style: none;
	font-family: 'Kanit', sans-serif;
	color: white;
	font-size: 0.9rem;
	margin-left: 2px;
	cursor: pointer;
}

.edit-form .btn-save:focus {
	outline: none;
}

.edit-form .btn-save:hover {
	background-color: #44b315;
}